import styled from "styled-components";
import theme from "Document.theme.js";

export const DropdownLink = styled.div`
	position: relative;
	text-align: center;
	.overlay {
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.25);
	}
	&.active .overlay {
		display: block;
	}
	.content {
		position: relative;
		background: #fff;
		.dropdown {
			.option {
				display: block;
			}
		}
	}
`;



